import { configureStore } from "@reduxjs/toolkit";
import userReducer from "./userSlice";

export const store = configureStore({
  reducer: {
    user: userReducer  // Khai bo 1 slide tn l user vi gi tr l userReducer c export  file userSlice
    // C th khai bo nhiu slide khc tng t
  }
});